<template>
    <div class="add-news-wrapper">
        <h2 class="title"></h2>
        <i-form v-ref:form-validate
                :model="formValidate"
                :rules="ruleValidate"
                :label-width="80">
            <Form-item label="id"
                       prop="id">
                <i-input :value.sync="formValidate.id"
                         placeholder="20170424..."></i-input>
            </Form-item>
            <Form-item label=""
                       prop="header">
                <i-input :value.sync="formValidate.header"
                         placeholder="..."></i-input>
            </Form-item>
    
            <Form-item label=""
                       prop="content">
                <i-input :value.sync="formValidate.content"
                         type="textarea"
                         :autosize="{minRows: 15,maxRows: 25}"
                         placeholder="..."></i-input>
            </Form-item>
            <Form-item>
                <i-button type="primary"
                          @click="handleSubmit('formValidate')"></i-button>
                <i-button type="ghost"
                          @click="handleReset('formValidate')"
                          style="margin-left: 8px"></i-button>
            </Form-item>
        </i-form>
    </div>
</template>
<script>
import { routerGo, getAllNews, setCurrentActiveKey, removeNewsAction, setCurrentNews, addNews } from '../../vuex/actions.js'
export default {
    data() {
        return {
            self: this,
            formValidate: {
                id: '',
                header: '',
                content: '',

            },
            ruleValidate: {
                id: [{
                    required: true, message: 'Id', trigger: 'blur'
                }],
                header: [{
                    required: true, message: '', trigger: 'blur'
                }],
                content: [{
                    required: true, message: '', trigger: 'blur'
                }],

            }
        }
    },
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.addNews(this.formValidate).then(() => {
                        this.$Message.success('');

                        setTimeout(() => {
                            this.self.routerGo('/news');
                        }, 200);
                    }).catch(err => {
                        console.log(err);
                        this.$Message.error('');
                    })
                } else {
                    this.$Message.error('!');
                }
            })
        },
        handleReset(name) {
            this.$refs[name].resetFields();
        }

    },
    vuex: {
        getters: {
        },
        actions: {
            routerGo,
            getAllNews,
            setCurrentActiveKey,
            removeNewsAction,
            setCurrentNews,
            addNews
        }
    }

}
</script>

<style lang="less">
.add-news-wrapper {
    width: 70%;
    margin-left: 30px;
    margin-top: 30px;
    >.title {
        position: relative;
        margin: 30px 0 30px 80px;
        font-size: 24px;
        font-weight: 300;
    }
}
</style>



